body {
  background-color: #fff;
}
@font-face {
  font-family: "Gotham ";
  src: url("/assets/fonts/Gotham-Rounded-Book.ttf") format('truetype');
}
.modal {
  display: block;
  position: fixed;
  z-index: 1;
  padding-top: 170px;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: #000;
  background-color: rgba(0,0,0,0.4);
}
.centre-text {
  margin-top: 20%;
  text-align: center;
  font-size: 30px;
}
/* Modal Content */
.modal-content {
  background-color: #fefefe;
  margin: auto;
  padding: 20px;
  border: 1px solid #888;
  width: 30%;
}
.modal-content .header {
  margin: 1% 1% 5% 1%;
  font-size: 1.2em;
}
.modal-content form {
  margin: 4% 1% 1% 1%;
}
.modal-content form label .user {
  margin-left: 33px;
}
.modal-content form label input {
  margin-left: 20px;
  margin-bottom: 20px;
  padding: 5px;
  width: 60%;
  background: #ecebeb;
  border: 1px solid #b5b2b2;
  box-shadow: 1px 1px #d4d4d4;
}
.modal-content form .search {
  padding: 4px;
  margin-top: 10px;
  width: 30%;
  height: 100%;
  margin-left: 31%;
  border: 1px solid #20b2aa;
  font-size: 1em;
  color: #fff;
  background: linear-gradient(#20b2aa, #278c87);
}
/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 20px;
}
.close:hover,
.close:focus {
  color: #000;
  text-decoration: none;
  cursor: pointer;
}
.artist-button {
  margin-left: 42%;
  padding: 8px;
  width: 200px;
  font-size: 20px;
  border: 1px solid #20b2aa;
  color: #fff;
  background: linear-gradient(#20b2aa, #278c87);
}
.result .header {
  margin: 4%;
}
.result .header .heading {
  float: left;
}
.result .header .clear {
  position: relative;
  top: 35px;
  color: #20b2aa;
  margin-left: 10px;
}
.result .content {
  width: 600px;
  height: 100px;
  clear: both;
  margin: 4%;
  border-bottom: 1px solid #c3bdbd;
  color: #a9a9a9;
}
.result .content .image {
  float: left;
  margin: 16px;
  width: 10%;
}
.result .content .image img {
  border-radius: 50%;
  width: 60px;
  height: 60px;
}
.result .content .description {
  margin: 10px;
  width: 80%;
  float: right;
}
.result .content .description label {
  color: #848282;
}
.result .content .description span {
  padding: 10px;
}
.result .content .description .long {
  height: 50px;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
}

